import React from "react";
import { useParams } from "react-router-dom";
import Product from "./Product";
import { CategoriesArr } from "./data";
import { ProductArr } from "./data";
import './ViewCategory.css'

function ViewCategory() {
    let params = useParams()
    let itemsArr = []

    function GetCategoryProducts(){
        for(let category of CategoriesArr ){
            console.log(category);
            if(category.name === params.slug){
                console.log(category,"ss");
                for(let product of ProductArr){
                    console.log(product);
                    if(category.items.includes(parseInt(product.id))){
                        itemsArr.push(product)
                    }
            }
        }
    }}
    GetCategoryProducts()
    console.log(itemsArr);
    const Products = itemsArr.map((ProductJson) =>
     <Product key={ProductJson.id} {...ProductJson}  />
);
    return ( <div className="Viewing-Component" >
    <h2>{params.slug}</h2>
    <div className="View-Category-items" >
        {Products}
    </div>
    </div> );
}

export default ViewCategory;