/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/alt-text */
import { useParams,Link } from 'react-router-dom';
import { ProductArr } from './data';
import './ViewProduct.css'
function ViewProduct() {
    const params = useParams()
    const product_details = ProductArr[params.slug]

	function AddToCart(e){
        e.target.innerText = "View Cart";
        if(localStorage.getItem("cart-items") == null){
            // console.log(localStorage.getItem("id"))
            localStorage.setItem('cart-items',e.target.getAttribute('product_id'))
        }else{
            localStorage.setItem('cart-items',`${localStorage.getItem("cart-items")},${e.target.getAttribute('product_id')}`)
        }

        document.querySelector("#Shopping-cart-Content").innerText = parseInt(document.querySelector("#Shopping-cart-Content").innerText) + 1
    }
	function IsInCart(data){
        

        try {
            if (localStorage.getItem('cart-items').split(',').includes(`${data.id}`) ){
                return true
            }else{
                return false
            }
            
        } catch (error) {
            return false
            
        }

        
    }


    return ( <>
    <div className="Viewing-Component">
		<div className="card">
			<div className="container-fliud">
				<div className="wrapper row">
					<div className="preview col-md-6">
						<img src={product_details.image} alt="" />
						
						
					</div>
					<div className="details col-md-6">
						<h3 className="product-title">{product_details.name}</h3>
						{/* <div className="rating">
							<div className="stars">
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star"></span>
								<span className="fa fa-star"></span>
							</div>
							<span className="review-no">41 reviews</span>
						</div> */}
						<p className="product-description">{product_details.detail}</p>
						<h4 className="price">current price: <span>&#8377;{product_details.offer_price}</span> <strike>&#8377;{product_details.original_price}</strike> </h4>
						{/* <p className="vote"><strong>91%</strong> of buyers enjoyed this product! <strong>(87 votes)</strong></p>
						<h5 className="sizes">sizes:
							<span className="size" data-toggle="tooltip" title="small">s</span>
							<span className="size" data-toggle="tooltip" title="medium">m</span>
							<span className="size" data-toggle="tooltip" title="large">l</span>
							<span className="size" data-toggle="tooltip" title="xtra large">xl</span>
						</h5>
						<h5 className="colors">colors:
							<span className="color orange not-available" data-toggle="tooltip" title="Not In store"></span>
							<span className="color green"></span>
							<span className="color blue"></span>
						</h5> */}
						<div className="action">
							{IsInCart(product_details) ? <Link to={'/my_cart'}  product_id={product_details.id}  className="btn-viewcart" ><i className="bi bi-cart3"></i>View cart</Link>:
							<button product_id={product_details.id} onClick={AddToCart} className="btn-addtocart" type="button"><i class="bi bi-cart3"></i>add to cart</button>}
							{/* <button className="like btn btn-default" type="button"><span className="fa fa-heart"></span></button> */}
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
    </> );
}

export default ViewProduct;