import './Cartitem.css'
import React,{useState,useEffect} from 'react';
import InputNumber from './InputNumber';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2'
function Cartitem(props) {
    const [quantity,setquantity] = useState(1)
    useEffect(() => {
      props.updateSubtotal(`${props.id}${quantity}`)
    //   console.log(props.updateSubtotal());
        console.log("changed");
    //   return () => {
    //     second
    //   }
    }, [quantity])


    function RemoveFromCart(){
        Swal.fire({
            title: `Are you sure want to remove <b><u> ${props.name}-${props.detail}</u> </b> from cart`,
            // showDenyButton: true,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            denyButtonText: 'No',
            icon:'warning',
            customClass: {
              actions: 'my-actions',
              cancelButton: 'order-1 right-gap',
              confirmButton: 'order-2',
              denyButton: 'order-3',
            }
          }).then((result) => {
            if (result.isConfirmed) {

          }})
    }
    
    return ( <>
    <div className='cart-item' quantity={quantity} >
        <div  className='left' style={{backgroundImage:`url(${props.image})`}} ></div>
        <div className='right' >
            <b>{props.name} {props.detail}</b>
            {/* <span>{props.detail}</span> */}
            <div className='grdclm' >
            <b>&#8377;{props.offer_price}</b>
            <strike>&#8377;{props.original_price}</strike>
            <span>save &#8377; {parseInt(props.original_price)-parseInt(props.offer_price)}</span>
            </div>
            {/* <ul>
                <li>Info 1</li>
                <li>Info 2</li>
                <li>Info 2</li>
            </ul> */}
            <div className='quantity' > <label htmlFor="">Quantity : </label> <InputNumber returnValue={setquantity}  default_value={quantity} /> </div>

            <div>Total price :<b>&#8377;{props.offer_price}</b><b> * {quantity} = </b><b>&#8377;{parseFloat(props.offer_price) * quantity}</b>  </div>

            <button className='remove-from-cart' onClick={RemoveFromCart} ><i class="bi bi-cart-dash"></i>remove from cart</button>
            <Link to={`/view_product=${props.id}`} className='knowmore grid-center' >Know more</Link>
        </div>
    </div>
    </> );
}

export default Cartitem;