import './InputNumber.css'
import React,{useState,useEffect} from 'react';

// props.offer_price
function InputNumber(props) {
    const [value,setValue] = useState(props.default_value || 0)

    useEffect(() => {
      props.returnValue(value)
    
    //   return () => {
    //     second
    //   }
    }, [props,value])
    
    function continuosincrement() {
        if(value !== (props.max||10)){
        setValue(value + 1)}
        // var inp = e.target.parentNode.querySelector(".ss-inp")
        // var inp = s().inp()

        // inp.innerHTML = parseInt(inp.innerHTML) + 1
        // s().btn().style.display = "block"
        // s().text().style.display = "none"
        
        // s().incpage().click()
        // setInterval(setTimeout(continuosincrement, 200))
    }

    function continuosdecerment(e) {
        if(value !== (props.min||0)){
        setValue(value - 1)}

        // var inp = e.target.parentNode.querySelector(".ss-inp")
        // var inp = s().inp()

        // inp.innerHTML = parseInt(inp.innerHTML) - 1
        // s().btn().style.display = "block"
        // s().text().style.display = "none"
        // s().decpage().click()
        // setInterval(setTimeout(continuosdecerment, 200))
    }

    return ( <>
    <div className="ss" >
        <span  onClick={continuosdecerment} className="ss-minus"  >-</span> 
        {/* <span className="ss-btn"  >Update</span>  */}
        {/* <span className="ss-text" >{props.text || ""} </span> */}
         <span className="ss-inp">{value}</span> 
         <span onClick={continuosincrement}  className="ss-add">+</span>
    </div>
    </> );
}

export default InputNumber;