import { useParams } from 'react-router-dom';
import { ProductArr } from '../components/data';
import {Link} from 'react-router-dom'

import './ViewProduct.css'

function ViewProduct() {
    const params = useParams()
    const product_details = ProductArr[params.slug]
    return ( <>
				<div className="adminviewproduct row">
                <span className='actiontop'>
                    <Link to={'/admin_dashboard/products'} ><i className="bi bi-arrow-left"></i></Link>
                
                <div className='random-2-btn' >
                    <button className='edit' ><i className="bi bi-pencil-square"></i> <span>Edit</span> </button>
                    <button className='delete' ><i className="bi bi-trash"></i> <span>Delete</span></button>

                </div>
                </span>
                    <div className="wrapper">
					<div className="preview col-md-6">
						<img src={product_details.image} alt="" />
						
						
					</div>
					<div className="details col-md-6">
						<h3 className="product-title">{product_details.name}</h3>
						{/* <div className="rating">
							<div className="stars">
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star"></span>
								<span className="fa fa-star"></span>
							</div>
							<span className="review-no">41 reviews</span>
						</div> */}
						<p className="product-description">{product_details.detail}</p>
						{/* <h4 className="price">current price: <span>&#8377;{product_details.offer_price}</span> <strike>&#8377;{product_details.original_price}</strike> </h4> */}
						{/* <ul>
							<li>{product_details.subdetails[0] || ""}</li>
							<li>{product_details.subdetails[1] || ""}</li>
						</ul> */}

						<div>
						<div>product id : <span>{product_details.id}</span> </div>
						<div>offer price : &#8377;<span>{product_details.offer_price}</span> </div>
						<div>selling price : &#8377;<span>{product_details.original_price}</span> </div>
						<div>stocked : <span>{product_details.stocked}</span> </div>
						</div>
						

					</div>
                    </div>

	</div>
    </> );
}
export default ViewProduct;