import Banner from "../components/Banner";
// import AllPrdctsCllction from "../components/AllPrdctsCllction";
import ItemSlider from "../components/ItemSlider";
import './Home.css'
import Product from "../components/Product";
import { ProductArr } from "../components/data";
import Category from "../components/Category";
import { CategoriesArr } from "../components/data";

function Home() {
    
    const Products = ProductArr.map((ProductJson) =>
     <Product key={ProductJson.id} {...ProductJson}  />
);
    const Categories = CategoriesArr.map((CategoryJson) =>
     <Category key={CategoryJson.id} {...CategoryJson}  />
);
    return ( <>

    <div id="Explore" >

    <Banner/>
    <ItemSlider title="Shop By Category" items={Categories} />
    <ItemSlider title="Top-Trending" items={Products} />
    <ItemSlider title="Best-selling" items={Products} />
    </div>
    {/* <Footer/> */}
    </> );
}

export default Home;